@media screen and (min-width: 1250px) {
  .container {
    width: 1200px;
    margin: 0 auto;
    padding: 20px 0 40px 0;
  }
  
  .title {
    font-size: 35px;
    font-weight: 500;
    text-align: center;
    padding: 20px 0 40px 0;
  }
  
  .results-grid {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 375px);
    justify-content: space-between;
    row-gap: 25px;
  }
}

@media screen and (max-width: 1249px) and (min-width: 800px) {
  .container {
    width: 780px;
    margin: 0 auto;
    padding: 20px 0 40px 0;
  }
  
  .title {
    font-size: 35px;
    font-weight: 500;
    text-align: center;
    padding: 20px 0 40px 0;
  }
  
  .results-grid {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 375px);
    justify-content: space-between;
    row-gap: 25px;
  }
}

@media screen and (max-width: 799px) {
  .container {
    width: 400px;
    margin: 0 auto;
    padding: 20px 0 40px 0;
  }
  
  .title {
    font-size: 35px;
    font-weight: 500;
    text-align: center;
    padding: 20px 0 40px 0;
  }
  
  .results-grid {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(1, 375px);
    justify-content: center;
    row-gap: 25px;
  }
}


.result {
  box-shadow: 0 0 6px rgb(0, 0, 0, 0.4);
  /* min-height: 0; */
  height: fit-content;
  font-size: 14px;
}

.result-inner {
  display: flex;

}

.result-image-container {
  display: grid;
  place-items: center;
}

.result-image {
  width: 150px;
  height: 150px;
}

.result-content {
  width: 100%;
}

.result-title {
  display: grid;
  align-items: center;
  font-weight: 600;
  height: 40px;
  grid-template-columns: 60px auto;
}

.inner-result-title {
  display: flex;
}

.result-number {
  background-color: #000;
  color: #ffffff;
  font-size: 12px;
  height: 100%;
  padding: 0 0 0 10px;
  display: flex;
  align-items: center;
}

.result-name {
  background-color: #F45239;
  height: 100%;
  line-height: 16px;
  padding: 0 0 0 10px;
  display: flex;
  align-items: center;
}

.result-detail-species {
  padding: 5px 0;
  text-align: center;
  border-bottom: 1px solid #DBDCDA;
  border-left: 1px solid #DBDCDA;
}

.result-detail-row {
  display: flex;
}

.result-detail-header {
  background-color: #DBDCDA;
}

.result-detail-column {
  width: 50%;
  padding: 5px 10px;  
}

.see-more {
  width: 100%;
  text-align: center;
  border-top: 1px solid #DBDCDA;
  padding: 10px 0;
  text-decoration: underline;
  font-weight: 600;
  color: #F45239;
  cursor: pointer;
  transition: 0.4s;
  &:hover {
    background-color: rgb(0, 0, 0, 0.1);
  }
}

.extra-details {
  width: 100%;
  display: flex;
  border-top: 1px solid #DBDCDA;
  font-size: 14px;
}

.extra-details-column {
  width: 50%;
}

.extra-details-row {
  display: flex;
}

.extra-details-header {
  padding: 10px 10px;  
  font-weight: 600;
  font-size: 12px;
}

.extra-details-value {
  padding: 8px 10px;  
}